import * as React from "react";
import { getWaterLevelData } from "../../services/noaa";
import { Title } from "./title/title";
import { Wave } from "./wave/wave";
import "./tide.scss";
export class Tide extends React.Component {
    constructor(props) {
        super(props);
        this.clickShare = () => {
        };
        this.clickRefresh = () => {
        };
        this.state = {};
    }
    componentDidMount() {
        const { waterLevel } = this.props;
        if (!waterLevel.data && !waterLevel.isRequesting) {
            getWaterLevelData()
                .then((data) => {
                waterLevel.onRequestEnd(data);
            });
            waterLevel.onRequestBegin();
        }
    }
    componentWillUnmount() {
        const { waterLevel } = this.props;
        if (!waterLevel.data && waterLevel.isRequesting)
            this.props.waterLevel.onRequestEnd(null);
    }
    render() {
        const { waterLevel } = this.props;
        if ((!waterLevel.data && !waterLevel.isRequesting) || (waterLevel.data && waterLevel.data.errors)) {
            return React.createElement("p", null, "Error....");
        }
        else if (!waterLevel.data && waterLevel.isRequesting) {
            return React.createElement("p", null, "Requesting...");
        }
        else {
            const data = waterLevel.data;
            return (React.createElement("div", { className: "tide" },
                React.createElement(Title, { waterLevel: data }),
                React.createElement(Wave, { waterLevel: data })));
        }
    }
}



