import * as React from "react";
import { getWaterLevelData } from "../../services/noaa";
import * as Time from "../../services/time";
import "./charts.scss";
export class Charts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    componentDidMount() {
        const { waterLevel } = this.props;
        if (!waterLevel.data && !waterLevel.isRequesting) {
            getWaterLevelData()
                .then((data) => {
                waterLevel.onRequestEnd(data);
            });
            waterLevel.onRequestBegin();
        }
    }
    componentWillUnmount() {
        const { waterLevel } = this.props;
        if (!waterLevel.data && waterLevel.isRequesting)
            this.props.waterLevel.onRequestEnd(null);
    }
    render() {
        const { waterLevel } = this.props;
        if ((!waterLevel.data && !waterLevel.isRequesting) || (waterLevel.data && waterLevel.data.errors)) {
            return React.createElement("p", null, "Error....");
        }
        else if (!waterLevel.data && waterLevel.isRequesting) {
            return React.createElement("p", null, "Requesting...");
        }
        else {
            const data = waterLevel.data;
            const previousPredictions = React.createElement("div", { className: "predictions predictions-previous" }, createTable(data.predictionsBeforeCurrent));
            const nextPredictions = React.createElement("div", { className: "predictions predictions-next" }, createTable(data.predictionsAfterCurrent));
            return (React.createElement("div", { className: "charts tab-view-bg" },
                React.createElement("header", null, "Wells, Maine"),
                previousPredictions,
                React.createElement("div", { className: "current" },
                    React.createElement("div", { className: "line" }),
                    React.createElement("div", { className: "current-text" },
                        "Current: ",
                        createPrettyTimeElement(data.current.time)),
                    React.createElement("div", { className: "line" })),
                nextPredictions));
        }
    }
}
function createPrettyTimeElement(date) {
    const pretty = Time.createPrettyTime(date);
    return (React.createElement("span", { className: "pretty" },
        React.createElement("span", { className: "pretty-time" }, pretty.time),
        React.createElement("span", { className: "pretty-ampm" }, pretty.ampm)));
}
function createPrettyMonthDay(date) {
    const month = date.toLocaleString("en-us", { month: "short" });
    const day = date.getDate();
    return `${month} ${day}`;
}
function createTable(predictions) {
    return (React.createElement("table", null,
        React.createElement("tbody", null, predictions.map((p, i) => {
            return (React.createElement("tr", { key: i },
                React.createElement("td", null, p.isHigh ? "High" : "Low"),
                React.createElement("td", null, createPrettyMonthDay(p.time)),
                React.createElement("td", null, createPrettyTimeElement(p.time)),
                React.createElement("td", null,
                    (Math.round(p.val * 100) / 100).toString().padEnd(4, "0"),
                    " ft")));
        }))));
}



